import React, {PureComponent} from 'react'
import {
  Row,
  Col,
  Table,
  Button,
  message,
  Spin
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {hasVariant, getVariant, removeVariant} from 'Variant'
import JsBarcode from 'jsbarcode'
import {BBC, RETURNORDER} from 'Mock'
const ORDER_LIST = 'wms/outWarehouseList'
import QRCode from 'qrcode.react'

@connect(
  state => ({...state.outWarehouseListSell}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
    this.columns = [{
      title: '',
      dataIndex: 'index',
      key: 'index',
      align: 'center',
      render: (text, record, index) => index + 1
    }, {
      title: '',
      dataIndex: 'productName',
      key: 'productName',
      align: 'center'
    }, {
      title: '',
      dataIndex: 'productCode',
      key: 'productCode',
      align: 'center'
    }, {
      title: '',
      dataIndex: 'productBarCode',
      key: 'productBarCode',
      align: 'center'
    }, {
      title: '',
      dataIndex: 'price',
      key: 'price',
      align: 'center'
    }, {
      title: '',
      dataIndex: 'unit',
      key: 'unit',
      align: 'center'
    }, {
      title: '',
      dataIndex: 'shippedQty',
      key: 'shippedQty',
      align: 'center'
    }, {
      title: '',
      dataIndex: 'amount',
      key: 'amount',
      align: 'center'
    }, {
      title: '',
      dataIndex: 'remark',
      key: 'remark',
      align: 'center'
    }, {
      title: '',
      dataIndex: 'fos_1',
      key: 'fos_1',
      align: 'center'
    }, {
      title: '',
      dataIndex: 'fos_2',
      key: 'fos_2',
      align: 'center'
    }]
    this.columns2 = [{
      title: '',
      dataIndex: 'index',
      key: 'index',
      align: 'center',
      render: (text, record, index) => index + 1
    }, {
      title: '',
      dataIndex: 'productName',
      key: 'productName',
      align: 'center'
    }, {
      title: '',
      dataIndex: 'productCode',
      key: 'productCode',
      align: 'center'
    }, {
      title: '',
      dataIndex: 'productBarCode',
      key: 'productBarCode',
      align: 'center'
    }, {
      title: '',
      dataIndex: 'unit',
      key: 'unit',
      align: 'center'
    }, {
      title: '',
      dataIndex: 'shippedQty',
      key: 'shippedQty',
      align: 'center'
    }, {
      title: '',
      dataIndex: 'amount',
      key: 'amount',
      align: 'center'
    }, {
      title: '',
      dataIndex: 'remark',
      key: 'remark',
      align: 'center'
    }]
    // this.SELL = ['27']
  }
  componentDidMount() {
    this._getAsyncData()
  }
  // tabskey
  _getAsyncData() {
    try {
      let ids = [],
        reqData = []
      if (hasVariant(ORDER_LIST)) {
        ids = getVariant(ORDER_LIST)
        console.log('ids is 111', ids)
      } else {
        removeVariant(ORDER_LIST)
      }
      if (!ids.length) return (0, this._closeWindow)(message.error(''))
      ids.map((o) => {
        reqData.push({
          id: o
        })
      })
      const {handleGetDetailPrint} = this.props
      const filterZero = getVariant('wms-print-filterZero')

      if (filterZero) {
        handleGetDetailPrint({data: reqData, searchParams: {filterZero: 'Y'}})
      } else {
        handleGetDetailPrint({data: reqData, searchParams: {filterZero: 'N'}})
      }
    } catch (err) {
      console.error(err)
    }
  }
  /*  */
  _closeWindow = () => window.setTimeout(() => window.close(), 2000)
  /*  */
  handlePrint = () => {
    if (window.print) {
      window.print()
    } else {
      document.execCommand('print')
    }
    try {
      let ids = [],
        reqData = []
      if (hasVariant(ORDER_LIST)) {
        ids = getVariant(ORDER_LIST)
        console.log(ids)
      } else {
        removeVariant(ORDER_LIST)
      }
      if (!ids.length) return (0, this._closeWindow)(message.error(''))
      ids.map((o) => {
        reqData.push({
          id: o
        })
      })
      const {handleUpdatePrintNum} = this.props
      handleUpdatePrintNum({data: reqData})
    } catch (err) {
      console.error(err)
    }
  }

  // 
  componentDidUpdate() {
    const {printData} = this.props
    printData.map((o) => {
      // JsBarcode(`#key${o.shippedResponse.orderCode}`, o.shippedResponse.orderCode + 555555, {
      //   width: 1.5,
      //   height: 30,
      //   fontSize: 12
      //   // displayValue: false
      // })
      if (BBC.indexOf(o.shippedResponse.businessType) == -1 && RETURNORDER.indexOf(o.shippedResponse.businessType) == -1 && o.shippedResponse.externalOrderCode) {
        JsBarcode(`#key${o.shippedResponse.externalOrderCode}`, o.shippedResponse.externalOrderCode, {
          width: 1.5,
          height: 30,
          fontSize: 12
          // displayValue: false
        })
      }
    })
  }
  render() {
    const {printLoading, printData, tableLoading, match: {params: {id}}} = this.props
    const printLoadingSty = {
      position: 'absolute',
      left: '50%',
      top: '50%',
      marginLeft: -20,
      marginTop: -20
    }
    return (
      <React.Fragment>
        {
          printData.length ? <Button className="noprint" type={'primary'} onClick={this.handlePrint}></Button> : (printLoading ? <h2 style={{textAlign: 'center'}}></h2> : <h2 style={{textAlign: 'center'}}></h2>)

        }
        {
          printData.map((o, i) => <section className="outboundOrderTemplate PageBreakAfter" key={i}>
              <div style={{position: 'relative', height: '110px', marginBottom: '20px', borderBottom: '1px solid #000', marginTop: '20px'}}>
                <h2 className="title">-{o.shippedResponse.businessTypeDesc}{o.shippedResponse.noSendFlagDesc && o.shippedResponse.noSendFlagDesc == '' && ''}</h2>
                <span
                style={{position: 'absolute', top: '-9px', left: '80px'}}
                >
                       <QRCode
                        value={o.shippedResponse.orderCode ? o.shippedResponse.orderCode : ''}  //value
                        size={100} //
                        fgColor="#000000"  //
                      />
                </span>
                {/* <img id={`key${o.shippedResponse.orderCode}`} style={{position: 'absolute', top: '-5px', left: '30px'}} /> */}
                {
                  BBC.indexOf(o.shippedResponse.businessType) == -1 && RETURNORDER.indexOf(o.shippedResponse.businessType) == -1 ?
                  <img id={`key${o.shippedResponse.externalOrderCode}`} style={{position: 'absolute', top: '13px', right: '30px'}} /> : <div style={{position: 'absolute', top: '10px', right: '10px', fontSize: '12px'}}>
                  <p style={{margin: '0px'}}>{o.printTime}</p>
                  <p style={{margin: '0px'}}>{`${o.shippedResponse.sendLocationCode}-${o.shippedResponse.sendLocationName}`}</p>
                </div>
                }
              </div>
              <div className='header'>
                {
                  BBC.indexOf(o.shippedResponse.businessType) != -1 ? <Row type="flex" justify="start">
                  <Col span={8} className='delBox'>
                    <label></label>
                    <span>{o.shippedResponse.orderCode}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label></label>
                    <span>{o.shippedResponse.stationName}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label></label>
                    <span>{o.productCategoryCount}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label></label>
                    <span>{o.amount}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label></label>
                    <span>{o.shippedResponse.receiveName}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label></label>
                    <span>{o.shippedResponse.receivePhoneNumber}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label></label>
                    <span>{o.shippedResponse.receiveAddress}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label></label>
                    <span>{o.shippedResponse.planDate}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label></label>
                    <span>{o.shippedResponse.sendTime}</span>
                  </Col>
                  <Col span={8} className='delBox'>
                    <label></label>
                    <span>{o.shippedResponse.remark}</span>
                  </Col>
                </Row> : RETURNORDER.indexOf(o.shippedResponse.businessType) != -1 ? <Row type="flex" justify="start">
                    <Col span={8} className='delBox'>
                      <label></label>
                      <span>{o.shippedResponse.batchCode}</span>
                    </Col>
                    <Col span={8} className='delBox'>
                      <label></label>
                      <span>{o.shippedResponse.orderCode}</span>
                    </Col>
                    <Col span={8} className='delBox'>
                      <label></label>
                      <span>{`${o.shippedResponse.supplierCode}-${o.shippedResponse.supplierName}`}</span>
                    </Col>
                    <Col span={8} className='delBox'>
                      <label></label>
                      <span>{o.productCategoryCount}</span>
                    </Col>
                    <Col span={8} className='delBox'>
                      <label></label>
                      <span>{o.amount}</span>
                    </Col>
                    <Col span={8} className='delBox'>
                      <label></label>
                      <span>{o.printer}</span>
                    </Col>
                    <Col span={8} className='delBox'>
                      <label></label>
                      <span>{o.shippedResponse.originOrderCode}</span>
                    </Col>
                    <Col span={8} className='delBox'>
                      <label></label>
                      <span>{o.shippedResponse.planDate}</span>
                    </Col>
                    <Col span={8} className='delBox'>
                      <label></label>
                      <span>{o.shippedResponse.sendTime}</span>
                    </Col>
                  </Row> : <Row type="flex" justify="start">
                    <Col span={8} className='delBox'>
                      <label></label>
                      <span>{o.shippedResponse.orderCode}</span>
                    </Col>
                    <Col span={8} className='delBox'>
                      <label></label>
                      <span>{o.shippedResponse.batchCode}</span>
                    </Col>
                    <Col span={8} className='delBox'>
                      <label></label>
                      <span>{o.shippedResponse.receiveLocationName}</span>
                    </Col>
                     <Col span={8} className='delBox'>
                      <label></label>
                      <span>{`${o.shippedResponse.transferLocationCode}-${o.shippedResponse.transferLocationName}`}</span>
                    </Col>
                    <Col span={8} className='delBox'>
                      <label></label>
                      <span>{`${o.shippedResponse.shopCode}-${o.shippedResponse.shopName}`}</span>
                    </Col>
                    <Col span={8} className='delBox'>
                      <label></label>
                      <span>{o.shippedResponse.externalOrderCode}</span>
                    </Col>
                    <Col span={8} className='delBox'>
                      <label></label>
                      <span>{o.shippedResponse.originOrderCode}</span>
                    </Col>
                    <Col span={8} className='delBox'>
                      <label></label>
                      <span>{o.printer}</span>
                    </Col>
                    <Col span={8} className='delBox'>
                      <label></label>
                      <span>{o.productCategoryCount}</span>
                    </Col>
                    <Col span={8} className='delBox'>
                      <label></label>
                      <span>{o.amount}</span>
                    </Col>
                    <Col span={8} className='delBox'>
                      <label></label>
                      <span>{o.shippedResponse.planDate}</span>
                    </Col>
                    <Col span={8} className='delBox'>
                      <label></label>
                      <span>{o.shippedResponse.sendTime}</span>
                    </Col>
                    <Col span={8} className='delBox'>
                      <label></label>
                      <span>{o.printTime}</span>
                    </Col>
                    <Col span={8} className='delBox'>
                      <label></label>
                      <span>{`${o.shippedResponse.sendLocationCode}-${o.shippedResponse.sendLocationName}`}</span>
                    </Col>
                  </Row>
                }
              </div>
              <div className='foot' style={{marginBottom: '10px'}}>
                <Table
                  rowKey={(record, index) => index}
                  loading={tableLoading}
                  columns={BBC.indexOf(o.shippedResponse.businessType) != -1 ? this.columns2 : this.columns}
                  dataSource={o.shippedOrderItemList}
                  pagination={false}
                  style={{marginTop: '6px'}}
                  bordered
                  size="middle"
                />
              </div>
              <div style={{textAlign: 'right'}}>
                <span style={{marginRight: '250px'}}></span>
                <span style={{marginRight: '200px'}}></span>
              </div>
            </section>)
        }
        <div style={printLoadingSty}>
          <Spin spinning={printLoading} size="large"/>
        </div>
      </React.Fragment>
    )
  }
}



